<template>
	<view>
		<!--自定义导航-->
		<uniNavBar left-icon="back"  statusBar :border="false" @clickLeft="goBack">
			<view class="flex align-center justify-center w-100">
				所有人可见<text class="iconfont icon-shezhi"></text>
			</view>
		</uniNavBar>
		<!--文本域-->
		<textarea v-model="content" value="" placeholder="说一句话吧~" class="px-2 w-auto " />
		<!--多图上传-->
		<uploadImage :show="show" ref="uploadImage" :list="imageList" @change="changeImage"></uploadImage>
		<!--底部操作条-->
		<view class="fixed-bottom bg-white flex align-center" style="height: 85rpx;">
			<view class="iconfont icon-caidan footer-btn animated" hover-class="jello"></view>
			<view class="iconfont icon-huati footer-btn animated" hover-class="jello"></view>
			<view class="iconfont icon-tupian footer-btn animated"
			 @click="iconClickEvent('uploadImage')"
			 hover-class="jello"></view>
			<view class="bg-main text-white ml-auto flex justify-center rounded mr-2 animated"
			 hover-class="jello"
			 style="width: 140rpx;height: 60rpx;">
				发送
			</view>
		</view>
	</view>
</template>

<script>
	import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue'
	import uploadImage from '@/components/common/upload-image.vue';
	export default {
		components:{
			uniNavBar,uploadImage
		},
		data() {
			return {
				content : "",
				imageList : [],
				// 是否已经弹出提示框
				showBack : false
			}
		},
		computed:{
			show(){
				return this.imageList.length > 0 
			}
		},
		// 监听返回
		onBackPress() {
			if((this.content !== '' || this.imageList.length > 0) && !this.showBack
			){
				uni.showModal({
					content:'是否要保存为草稿',
					showCancel:true,
					cancelText:'不保存',
					confirmText:'保存',
					success : res => {
						if(res.confirm){
							this.store()
						}else{
							uni.removeStorage({
								key:'add-input',
								
							})
						}
						// 手动执行返回
						uni.navigateBack({
							delta:1
						})
					}
				})
				this.showBack = true
				return true
			}
		},
		// 页面加载时
		onLoad(){
			uni.getStorage({
				key : 'add-input',
				success: (res) => {
					if(res.data){
						
						let result = JSON.parse(res.data);
						this.content = result.content;
						this.imageList = result.imageList;
						console.log(this.imageList)
					}
				}
			})
		},
		methods: {
			// 底部图片点击事件
			iconClickEvent(e){
				switch(e){
					case 'uploadImage' : this.$refs.uploadImage.chooseImage();break;
				}
			},
			// 选中图片
			changeImage(e){
				this.imageList = e
			},
			// 保存操作
			store(){
				let obj = {
					content : this.content,
					imageList : this.imageList
				}
				// 保存为本地存储
				uni.setStorage({
					key : 'add-input',
					data : JSON.stringify(obj)
				})
			},
			// 回退上一页
			goBack(){
				uni.navigateBack({
					delta:1
				})
			}
		}
	}
</script>

<style>
.footer-btn{
	width: 65rpx;
	height: 65rpx;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 50rpx;
}
</style>
